<template>
  <div
    id="app"
    class="small-container"
  >
    <h3>Employee Table</h3>
    <employee-form @add:employee="addEmployee" />
    <employee-table
      v-bind:employees="employees"
      @delete:employee="deleteEmployee"
      @edit:employee="editEmployee"
    />
  </div>
</template>


<script>
  import EmployeeTable from "./components/EmployeeTable.vue";
  import EmployeeForm from "@/components/EmployeeForm.vue";

  export default {
    name: "app",
    components: {
      EmployeeTable,
      EmployeeForm
    },
    data() {
      return {
        employees: []
      };
    },
    mounted() {
      //mounted is Vue Instance lifecycle method that executes its contents at the corresponding point in app's life cycle
      this.getEmployees();
    },
    methods: {
      async getEmployees() {
        try {
          const response = await fetch(
            "https://jsonplaceholder.typicode.com/users"
          );
          const data = await response.json();
          this.employees = data;
        } catch (error) {
          // eslint-disable-next-line
          console.error(error);
        }
      },

      async addEmployee(employee) {
        try {
          const response = await fetch(
            "https://jsonplaceholder.typicode.com/users",
            {
              method: "POST",
              body: JSON.stringify(employee),
              headers: { "Content-type": "application/json; charset=UTF-8" }
            }
          );
          const data = await response.json();
          this.employees = [...this.employees, data];
        } catch (error) {
          // eslint-disable-next-line
          console.error(error);
        }
      },

      async editEmployee(id, updatedEmployee) {
        try {
          const response = await fetch(
            `https://jsonplaceholder.typicode.com/users/${id}`,
            {
              method: "PUT",
              body: JSON.stringify(updatedEmployee),
              headers: { "Content-type": "application/json; charset=UTF-8" }
            }
          );
          const data = await response.json();
          this.employees = this.employees.map(employee =>
            employee.id === id ? data : employee
          );
        } catch (error) {
          // eslint-disable-next-line
          console.error(error);
        }
      },

      async deleteEmployee(id) {
        //when delete employee event is emitted, filter out the employee whose ID
        //was passed wtih event object
        try {
          await fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
            method: "DELETE"
          });
          this.employees = this.employees.filter(
            //for each employee in employees, only keep those whose id isn't the id in event
            employee => employee.id !== id
          );
        } catch (error) {
          // eslint-disable-next-line
          console.error(error);
        }
      },

      generateEmployeeIDs() {
        // eslint-disable-next-line
        console.log("hang on");
      }
    }
  };
</script>



<style>
  button {
    background: #009435;
    border: 1px solid #009435;
  }

  .small-container {
    max-width: 680px;
  }

  employee-table {
    box-sizing: border-box;
  }
  table {
    /* border: 2px solid white; */
    border-radius: 5px;
    box-shadow: 0px 5px 15px 2px rgba(78, 78, 78, 0.493);
  }
  td {
    width: 210px;
    background: rgba(221, 233, 238, 0.219);
  }
  th {
    padding: 10px;
    background: rgba(159, 169, 173, 0.37);
    border-radius: 2px;
    border: 2px solid rgba(255, 255, 255, 0.842);

    border-bottom: 2px solid rgba(155, 155, 155, 0.664);
    box-shadow: 0px 3px 2px 2px rgba(112, 112, 112, 0.158);
  }

  button {
    margin: 0 0.5rem 0 0;
  }
  #button-box {
    padding-left: 10px;
  }
  input {
    box-sizing: border-box;
    padding: 7px 5px;
    background: white;
    border: 1px solid rgba(165, 165, 165, 0.651);
  }
</style>
