<template>
  <div id="employee-form">
    <form @submit.prevent="handleSubmit">
      <label>Employee Name</label>
      <input
        ref="first"
        type="text"
        :class="{'has-error': submitting && invalidName}"
        v-model="employee.name"
        @focus="clearState"
        @keypress="clearState"
      />

      <!-- @focus & @ keypress make sure the fields are clear
			when submitted and then cursor moved back to
			first input  -->

      <label>Employee Email</label>
      <input
        type="text"
        :class="{'has-error': submitting && invalidEmail}"
        v-model="employee.email"
        @focus="clearState"
      />

      <!-- feedback messages for input validation -->
      <p
        v-if="error && submitting"
        class="error-message"
      >
        Fill out these fields or you're dumb.
      </p>
      <p
        v-if="success"
        class="success-message"
      >
        You're so smart. Input = valid.
      </p>

      <button>Add Employee</button>
    </form>
  </div>
</template>

<script>
  export default {
    name: "employee-form",
    data() {
      return {
        submitting: false,
        error: false,
        success: false,
        employee: {
          name: "",
          email: ""
        }
      };
    },
    methods: {
      handleSubmit() {
        this.submitting = true;
        this.clearState();

        if (this.invalidName || this.invalidEmail) {
          this.error = true;
          return; //end execution of function/don't pass any data to App component
        }

        this.$emit("add:employee", this.employee);
        this.employee = {
          name: "",
          email: ""
        };
        this.$refs.first.focus();

        //if we made it this far, then passed validation; update state props to reflect
        this.success = true;
        this.error = false;
        this.submitting = false;
      },

      clearState() {
        //reset success and error state values for new submission
        this.success = false;
        this.error = false;
      }
    },
    computed: {
      // input validations, auto-compute responsively to changes;
      // they will return true if inputs are blank at submit
      invalidName() {
        return this.employee.name === "";
      },

      invalidEmail() {
        return this.employee.email === "";
      }
    }
  };
</script>

<style scoped>
  form {
    margin-bottom: 2rem;
  }

  [class*="-message"] {
    font-weight: 500;
  }

  .error-message {
    color: #d33c40;
  }

  .success-message {
    color: #32a95d;
  }
  input {
    box-sizing: border-box;
    padding: 7px 5px;
    background: white;
    border: 1px solid rgba(165, 165, 165, 0.651);
  }
</style>