
<template>
  <div id="employee-table">
    <table>
      <thead>
        <tr id="header-row">
          <th>Name</th>
          <th id="middle-header">Email</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="employee in employees"
          :key="employee.id"
        >
          <td v-if="editing === employee.id">
            <!-- edit mode version of each input element that displays when edit is selected for that row -->
            <input
              type="text"
              v-model="employee.name"
            >
          </td>
          <td v-else>{{ employee.name }}</td>
          <td v-if="editing === employee.id">
            <!-- edit mode version of each input element that displays when edit is selected for that row -->
            <input
              type="text"
              v-model="employee.email"
            >
          </td>
          <td v-else>{{ employee.email }}</td>

          <td v-if="editing === employee.id">
            <div id="button-box">
              <!-- edit mode version of each input element that displays when edit is selected for that row -->
              <button @click="editEmployee(employee)">Save</button>
              <button
                @click="cancelEdit(employee)"
                class="muted-button"
              >Cancel</button>
            </div>
          </td>
          <td v-else>
            <div id="button-box">
              <!-- on click, the "editing" state property will be updated with selected emp's id -->
              <button @click="editMode(employee)">Edit</button>

              <!-- emit custom delete event, and send the employee's id as payload to App.vue -->
              <button @click="$emit('delete:employee', employee.id)">Delete</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
  export default {
    name: "employee-table",
    props: {
      employees: Array
    },

    data() {
      return {
        editing: null
      };
    },

    methods: {
      editMode(employee) {
        //copy the employee object to a temp cache backup in case of cancel button clcick

        this.cachedEmployee = Object.assign({}, employee);
        this.editing = employee.id;
      },

      cancelEdit(employee) {
        //cancelled, so take the stored emp object and assign it back to current employee according to id number
        Object.assign(employee, this.cachedEmployee);
        this.editing = null;
      },

      editEmployee(employee) {
        //prevent from switching back to non-edit mode if either field is blank
        if (employee.name === "" || employee.email === "") {
          return;
        }

        this.$emit("edit:employee", employee.id, employee);
        this.editing = null;
      }
    }
  };
</script>


<style>
  employee-table {
    box-sizing: border-box;
  }
  table {
    /* border: 2px solid white; */
    border-radius: 5px;
    box-shadow: 0px 5px 15px 2px rgba(78, 78, 78, 0.493);
  }
  td {
    width: 210px;
    background: rgba(221, 233, 238, 0.219);
  }
  th {
    padding: 10px;
    background: rgba(159, 169, 173, 0.37);
    border-radius: 2px;
    border: 2px solid rgba(255, 255, 255, 0.842);

    border-bottom: 2px solid rgba(155, 155, 155, 0.664);
    box-shadow: 0px 3px 2px 2px rgba(112, 112, 112, 0.158);
  }

  button {
    margin: 0 0.5rem 0 0;
  }
  #button-box {
    padding-left: 10px;
  }
  input {
    box-sizing: border-box;
    padding: 7px 5px;
    background: white;
    border: 1px solid rgba(165, 165, 165, 0.651);
  }
</style>